@import url(https://fonts.googleapis.com/css?family=Montserrat|Raleway:900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div#headerMainContainer {
    width: 100%;
    height: 5.3rem;
    background-color: white;
    display: flex;
    align-items: center;
}

.col-3 {
    display: inline-block;
    width: 30%;
    height: 100%;
}

.col-4 {
    display: inline-block;
    height: 100%;
    width: 40%;
}

div.stankinLogoContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

img.stankinLogo {
    width: 7rem;
}

div.menuItems {
    float: right;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

div#menuLocaleSeparator {
    display: inline-block;
    height: 60%;
    width: 1px;
    background: blue;
    margin-right: 1.3rem;
}

span.localeBadge {
    font-family: 'Montserrat', sans-serif;
    margin-right: 3.5rem;
    font-size: 1.3rem;
    -webkit-transition: 0.2s linear;
    transition: 0.2s linear;
}

span.localeBadge:hover {
    cursor: pointer;
    color: #3d82e3;
}

@media (max-width: 900px) {
    span.localeBadge {
        margin-right: 2rem;
    }
}

@media (max-width: 400px) {
    span.localeBadge {
        margin-right: 1.2rem;
        font-size: 1.1rem;
    }

    div#menuLocaleSeparator {
        height: 48%;
        margin-right: 0.8rem;
    }
}

div.aboutSectionMainContainer {
    padding-top: 3em;
    background-color: #3d82e3;
}

.aboutSectionTitle {
    color: white;
    font-size: 34pt;
    font-family: 'Montserrat', sans-serif;
}

span.aboutTitleBold {
    font-weight: bold;
}

div.handContainer {
    width: 40%;
}

div.handContainer img#handImage {
    height: 40em;
    margin-top: 1rem;
}

div.descriptionMainContainer {
    display: flex;
    justify-content: center;
}

div.appDescriptionContainer {
    width: 40%;
    height: 100%;
    text-align: left;
}

div.appDescriptionContainer > div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

div#appDescriptionText {
    width: 80%;
}

div.appDescriptionContainer div#storeBadgesContainer {
    width: 100%;
    margin-top: 45px;
}

div.appDescriptionContainer div#storeBadgesContainer  img {
    display: inline;
    width: 12rem;
    margin: 1rem 1rem 1rem 0;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

div.appDescriptionContainer div#storeBadgesContainer  img:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

@media (max-width: 800px) {
    div.handContainer {
        display: none;
    }

    div.appDescriptionContainer {
        width: 70%;
        text-align: center;
    }

    div.appDescriptionContainer > div {
        align-items: center;
    }
}

@media (max-width: 570px) {
    div.appDescriptionContainer {
        width: 85%;
    }

    div.appDescriptionContainer div#storeBadgesContainer {
        margin: 2em 0;
    }

    h3.ant-typography {
        margin: 2.1rem 0!important;
    }
}

@media (max-width: 380px) {
    div.appDescriptionContainer {
        width: 90%;
    }

    div.appDescriptionContainer div#storeBadgesContainer img {
        width: 9rem;
        margin: 0.5rem 0.5rem;
    }
}


div.iconsGroupContainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

div.iconsGroupContainer > div {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 1.5rem;
}

div.groupIcon {
    margin: 2.5rem 3.8rem;
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}

div.selectedIcon {
    width: 2rem;
    height: 2rem;
    /*background: #3d81e3;*/
    box-shadow: 10px 10px 50px 20px grey;
    padding-bottom: 15px;
}

.sliderIcon {
    width: 6rem;
}

.circleIcon {
    border-radius: 50%;
    background: white;
}

.ant-carousel .slick-slide {
    height: 543px!important;
}

.slideContainer {
    max-width: 30rem!important;
    margin-top: 2rem;
}

.phoneFrameImage {
    background-image: url(/stankin-info/static/media/phoneFrame.faf37b5e.svg);
    width: 317px;
    height: 455px;
    display: inline-block;
    position: relative;
}

.phoneFrameImage > img {
    width: 217px;
    position: absolute;
    left: 50px;
    top: 71px;
    z-index: -1;
}

@media (max-width: 660px) {
    .slideContainer {
        width: 95%!important;
    }

    div.groupIcon {
        margin: 2.5rem 2rem;
    }

    .sliderIcon {
        width: 5rem;
    }

    div.selectedIcon {
        width: 1rem;
        height: 1rem;
        box-shadow: 6px 6px 30px 10px grey;
        padding-bottom: 15px;
    }
}

@media (max-width: 410px) {
    .slideContainer {
        margin-top: 5px;
    }
}

@media (max-width: 400px) {
    div.groupIcon {
        margin: 2.5rem 1.5rem;
    }

    .sliderIcon {
        width: 4rem;
    }
}

div#featureSectionMainContainer {
    margin-top: 3rem;
}

#featureSectionTitle {
    color: #11116e;
    font-size: 34pt;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
}

div.authorsMainSection {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    width: 100%;
    min-height: 400px;
    padding: 1rem 0;
    background: #3940b3;
}

div.authors {
    display: flex;
    max-width: 1200px;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.authors > span {
    display: block;
    width: 100%;
    color: white;
    font-size: 34pt;
    font-weight: 300;
    margin-bottom: 1rem;
}

div.authors > span > span {
    font-weight: bold;
}

div.authors > div.authorsGroup {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}

div.authorsGroup > div.authorInfo {
    width: 300px;
    height: 120px;
    display: flex;
    /*flex-direction: column;*/
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
}

div.authorsGroup > div.authorInfo > div {
    height: 100%;
    width: 170px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 1rem;
}

span.ant-avatar {
    width: 90px;
    height: 90px;
    line-height: 90px;
}

.ant-avatar.ant-avatar-icon {
    font-size: 45px!important;
}

div.authorsGroup > div.authorInfo > div > span {
    color: white;
    text-align: left;
}

div.authorsGroup > div.authorInfo > div > span.authorName {
    font-weight: bold;
    font-size: 11pt;
}

@media (max-width: 1300px) {
    div.authors {
        max-width: 900px;
    }
}

@media (max-width: 900px) {
    div.authors {
        max-width: 600px;
    }
}

@media (max-width: 630px) {

    div.authors {
        max-width: 400px;
    }

    span.ant-avatar {
        width: 64px;
        height: 64px;
        line-height: 64px;
        margin-bottom: 7px;
    }

    .ant-avatar.ant-avatar-icon {
        font-size: 32px!important;
    }

    div.authorsGroup > div.authorInfo {
        flex-direction: column;
        width: 190px;
        height: 138px;
    }

    div.authorsGroup > div.authorInfo > div {
        width: 175px;
        height: 60px;
        align-items: center;
        margin-left: 0;
    }

    div.authorsGroup > div.authorInfo > div > span {
        text-align: center;
    }
}

@media (max-width: 390px) {
    div.authors {
        max-width: 260px;
    }

    div.authorsGroup > div.authorInfo {
        flex-direction: column;
        width: 130px;
        height: 160px;
    }

    div.authorsGroup > div.authorInfo > div {
        height: 86px;
    }

    div.authorsGroup > div.authorInfo > div > span {
        max-width: 110px;
    }
}


div.footerMainSection {
    font-family: 'Montserrat', sans-serif;
    width: 100%;
    min-height: 350px;
    background: #21265d;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

div.stankinFooterLogoContainer > div.stankinFooterLogo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.stankinFooterLogo > img {
    width: 14rem;
}

div.stankinFooterLogo > span {
    color: white;
    font-size: 18pt;
    margin-top: 5px;
    font-weight: bold;
}

.App {
  text-align: center;
}

span.bold {
  font-weight: bold;
}

@media (min-width: 800px) {
  div.handContainer {
    display: block;
  }
}

@media (min-width: 550px) {

}

